/* Base Reset */
body,
html {
  margin: 0;
  padding: 0;
  font-family: "Segoe UI", "Inter", sans-serif;
  background: #f5f7fa;
  height: 100vh;
  overflow: hidden;
}

.page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

/* Main Card */
.main {
  background: white;
  padding: 40px;
  border-radius: 20px;
  max-width: 500px;
  width: 90%;
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.08);
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 24px;
}

/* Profile Image */
.profile-pic {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  object-fit: cover;
  margin: 0 auto;
  box-shadow: 0 0 0 4px #e5e7eb;
}

/* Name + Subtitle */
.name {
  font-size: 28px;
  font-weight: 600;
  color: #111;
  margin: 0;
}

.subtitle {
  font-size: 16px;
  color: #555;
  margin-top: -8px;
}

/* Buttons */
.btn.primary {
  padding: 12px 24px;
  background: #2563eb;
  color: white;
  border-radius: 8px;
  text-decoration: none;
  font-weight: 500;
  font-size: 15px;
  transition: background 0.3s ease;
}

.btn.primary:hover {
  background: #1d4ed8;
}

/* Link cards */
.links {
  display: flex;
  justify-content: space-between;
  gap: 16px;
  margin-top: 12px;
  flex-wrap: wrap;
}

.info-card {
  background: #f1f5f9;
  border-radius: 12px;
  padding: 16px;
  flex: 1;
  min-width: 200px;
  text-align: left;
}

.info-card h3 {
  margin: 0 0 6px 0;
  font-size: 16px;
  color: #111;
}

.info-card p {
  margin: 0 0 8px;
  font-size: 14px;
  color: #555;
}

.info-card a {
  font-size: 14px;
  color: #2563eb;
  text-decoration: none;
}

.info-card a:hover {
  text-decoration: underline;
}
